* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: rgb(1, 2, 4);
  --bg-variant: rgb(36, 13, 68);
  --color-primary: rgb(238, 146, 92, 0.8);
  --color-primary-variant: rgba(79, 172, 178);
  --color-white: rgb(248, 244, 181);
  --color-light: rgba(215, 252, 253);

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;

}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: 'Lato', sans-serif;
  background: rgb(1, 2, 4);
  background: var(--color-bg);
  color: rgb(248, 244, 181);
  color: var(--color-white);
  line-height: 1.7;
  background-image: url(/static/media/1.e46a0982.jpg);
  background-attachment: fixed;

}

/* ============== GENERAL STYLES ================ */

.transparency {
  background-color: rgb(0, 0, 0, 0.5);
}
.container {
  width: 75%;
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1, h2, h3 ,h4, h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
  background: rgb(0, 0, 0, 0.8);
}

section > h2, section > h5 {
  text-align: center;
  color: rgba(215, 252, 253);
  color: var(--color-light);

}

section > h2 {
  color: rgb(238, 146, 92, 0.8);
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light {
  color: rgba(215, 252, 253);
  color: var(--color-light)
}

a {
  color: rgb(238, 146, 92, 0.8);
  color: var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);
}

a:hover {
  color: rgb(248, 244, 181);
  color: var(--color-white);
}

.btn {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  display: inline-block;
  color: rgb(238, 146, 92, 0.8);
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid rgb(238, 146, 92, 0.8);
  border: 1px solid var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);
  margin-bottom: 30px;
}

.btn:hover {
  background: rgb(248, 244, 181);
  background: var(--color-white);
  color: rgb(1, 2, 4);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: rgb(238, 146, 92, 0.8);
  background: var(--color-primary);
  color: rgb(1, 2, 4);
  color: var(--color-bg);
}

img {
  display:block;
  width: 100%;
  object-fit: cover;
}

/* ===================== MEDIA QUERIES (MEdium Devices) =====================*/
@media screen and (max-width: 1024px) {
  .container {
    width: 86%;
    width: var(--container-width-md)
  }
  section {
    margin-top: 6rem;
  }
}

/* ===================== MEDIA QUERIES (Small Devices) =====================*/
@media screen and (max-width: 1024px) {
  .container {
    width: 90%;
    width: var(--container-width-sm)
  }
  section > h2{
    margin-bottom: 2rem;
  }
}
nav {
    background: rgba(0, 0 , 0 , 0.3);
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;

}

nav a:hover {
    background: rgba(0, 0, 0, 0.3);
}

nav a.active {
    background: var(--color-primary);
    color: var(--color-bg);
}
header {
    height: 100vh;
    padding: 7rem;
    overflow: hidden;
    /* background-color: rgb(0, 0, 0, 0.3); */
    background-size: cover;
    background-attachment: fixed;
}

.transparent__background {
    text-align: center;
}

.baby__background {
    display: inline-block;
    text-align: left;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* ====================== CTA ========================== */
.cta {
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;

}

/* ====================== Header Socials ========================== */
.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
    padding-left: 10px;
}

/* .header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
} */
.big__size {
    height: 1em;
    width: 1em;
    transition: .4s;
    margin-bottom: 1em;
    margin-left: 1em;
}

.big__size:hover{
    height: 2em;
    width: 2em;
    transition: .4s;
    margin-bottom: 0em;
    margin-left: 0em;
}


/* ====================== ME ========================== */
.me {
    background: linear-gradiant(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}
/* ====================== scroll Down ========================== */
.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
    padding-left: 10px;
    padding-right: 10px;
    margin-right: .2rem;
}

.scroll__down:hover {
    margin-right: 0em;
    font-size: 1.1rem;
}

/* ===================== MEDIA QUERIES (MEdium Devices) =====================*/
@media screen and (max-width: 1024px) {
    header {
        height: 68vh;

    }
}

/* ===================== MEDIA QUERIES (small Devices) =====================*/
@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .header__socials, .scroll__down {
        display: none;
    }
}
.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
        transparent,
        var(--color-primary),
        transparent
    );
    display: grid;
    place-items: center;
}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover {
    transform: rotate(0);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    margin-top: 40px;
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
    letter-spacing: .3px;
}

.about__margin {
    margin-bottom: 100px;
}

/* ===================== MEDIA QUERIES (MEdium Devices) =====================*/
@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }
    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about__content p {
        margin: 1rem 0 1.5rem;
    }
}

/* ===================== MEDIA QUERIES (small Devices) =====================*/
@media screen and (max-width: 600px) {
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;

    }
    .about__cards {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
    .about__content {
        text-align: center;
    }
    .about__content p {
        margin: 1.5rem 0;
    }

}
.experience__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}

.experience__container > div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
}

.experience__container > div:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.experience__container > div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experience__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.experience__details {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-left: .5em;
}

.experience__details-icon {
    margin-top: 6px;
    color: var(--color-primary);
}

/* ===================== MEDIA QUERIES (MEdium Devices) =====================*/
@media screen and (max-width: 1024px) {
    .experience__container {
        grid-template-columns: 1fr;
    }

    .experience__container > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .experience__content {
        padding: 1rem;
    }

    .experience__details{
        margin-left: 30%;
    }
}

/* ===================== MEDIA QUERIES (small Devices) =====================*/
@media screen and (max-width: 600px) {
    .experience__container {
        grid-gap: 1rem;
        gap: 1rem;
    }

    .experience__container > div {
        width: 100%;
        padding: 2rem 1rem;
    }
}
.portfolio__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}

.portfolio__item {
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portfolio__item:hover {
    border-color: var(--color-primary-variant);
    background: transparent;
}

.portfolio__item-image {
    border-radius: 1.5rem;
    overflow: hidden;
}

.portfolio__item h3 {
    margin: 1.2rem 0 2rem;
}

.portfolio__item-cta {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
}

/* ===================== MEDIA QUERIES (MEdium Devices) =====================*/
@media screen and (max-width: 1024px) {
    .portfolio__container {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1.2rem;
        gap: 1.2rem;
    }
}

/* ===================== MEDIA QUERIES (small Devices) =====================*/
@media screen and (max-width: 600px) {
    .portfolio__container {
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
}
.container.container__container {
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
}

.contact__options {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    margin: auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-bottom: 25px;
}

.contact__option {
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}

.contact__option:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
}

.contact__option-icon {
    font-size: 1.5 rem;
    margin-bottom: 0.5rem;
}

.contact__option a {
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
    
}

form {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    max-width: 65%;
    margin: auto;
}

input, textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-white);
}

/* ===================== MEDIA QUERIES (MEdium Devices) =====================*/
@media screen and (max-width: 1024px) {
    .container.contact__container {
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
}

/* ===================== MEDIA QUERIES (small Devices) =====================*/
@media screen and (max-width: 600px) {
    .container.contact__container {
        width: var(--container-width-sm);
    }

}
footer {
    /* background-color: rgb(0, 0, 0, 0.3); */
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a {
    color: var(--color-primary);
}

.footer__logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__socials {
    display: flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer_-socials a {
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius:0.7rem ;
    display: flex;
    border: 1px solid transparent;

}

.footer__socials a:hover {
    background: transparent;
    color: var(--color-primary);
    border-color: var(--color-bg);
}

.footer__copyright {
    margin-bottom: 4rem;
    color: var(--color-primary);
}

/* ===================== MEDIA QUERIES (small Devices) =====================*/
@media screen and (max-width: 600px) {
    .permalinks {
        flex-direction: column;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }
    .footer__socials {
        margin-bottom: 2.6rem;
    }

}
